* {
  box-sizing: border-box;
}

html {
  font-size: 18px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #222;
}

/*
App.js
==============================
*/
.App {
  padding: 20px;
  font-weight: bold;
  color: white; }

/*
Dashboard.js
==============================
*/
.Dashboard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  min-width: 700px; }

/*
Widget.js
==============================
*/
.Widget {
  flex-shrink: 0;
  width: 580px;
  height: 580px;
  margin: 20px;
  background: #272727;
  border-radius: 10px; }

/*
Meter.js
==============================
*/
.Meter {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%; }

.Meter__get-data-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 50px;
  height: 50px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background: none; }
  .Meter__get-data-btn:hover {
    background: #444; }
  .Meter__get-data-btn img {
    padding: 10px;
    width: 100%; }

.Meter__val {
  font-size: 80px; }

.Meter__dial {
  position: relative;
  width: 400px;
  height: 200px;
  margin: 40px 0; }

.Meter__arc-clip {
  width: 400px;
  height: 200px;
  overflow: hidden; }

.Meter__arc {
  width: 400px;
  height: 400px;
  border: 20px solid;
  border-radius: 50%;
  border-color: #444;
  border-left-color: #0ebeff;
  border-bottom-color: #0ebeff;
  transition: -webkit-transform 800ms;
  transition: transform 800ms;
  transition: transform 800ms, -webkit-transform 800ms;
  will-change: transform; }

.Meter__needle {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 10px;
  height: 170px;
  margin: 0 auto;
  background: #ffdd40;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  transition: -webkit-transform 800ms;
  transition: transform 800ms;
  transition: transform 800ms, -webkit-transform 800ms;
  will-change: transform; }

.Meter__needle-base {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -15px;
  width: 30px;
  height: 30px;
  margin: 0 auto;
  background: #ffdd40;
  border-radius: 50%; }

.Meter__min,
.Meter__max {
  position: absolute;
  font-size: 24px; }

.Meter__min {
  bottom: -0.5em;
  right: 103%; }

.Meter__max {
  bottom: -0.5em;
  left: 103%; }

